.search 
  text-align: center
  background-image: url("../img/room-2559790_1280.jpg")  
  background-repeat: no-repeat
  background-size: cover
  background-position: center
  background-attachment: fixed
  position: relative
  min-height: 50vh
  width: 100vw
  form
    background-color: rgba(255, 255, 255, 0.7)
    height: 100%
    padding: 10vh 0
    font-size: 1.3rem
    p
      font-size: 1.5rem
      font-weight: bold
    h3
      text-transform: uppercase
      margin: 5vh 0
      z-index: 1
      font-weight: bold
      font-size: 2.5rem
    button
      display: block
      margin: 3vh auto
      padding: 1vh 20vw
      text-transform: uppercase
      background-color: rgba(255, 255, 255, 0.7)
      border: 3px solid black
      border-radius: 20px
    input
      border: none
      border-radius: 10px
      padding: 1vh 5vw
      color: white
      background-color: black
      text-align: center
    .search-list
      display: none
    .search-list.on
      margin-top: 10vh
      padding-top: 5vh
      display: block
      font-weight: bold
      background-color: black
      color: white
      ul
        margin-top: 5vh
        li 
          color: black
          background-color: white
          font-weight: bold
          padding: .5vh 0
          border-bottom: 1px solid black
          cursor: pointer
    
  
    