.titleButton 
  position: relative
  height: 40vh
  font-family: "Cormorant SC", serif
  font-size: 2rem
  background-color: rgb(236, 243, 250)
  cursor: pointer
  overflow: hidden
  border: 2px solid black
  font-weight: 500
  img 
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    height: 100%
    min-width: 100%
    transition: 0.5s

.titleButton:hover > img  
  height: 120%
