header 
  height: 100vh
  width: 100vw
  position: relative
  .img 
    height: 100%
    width: 100%
    position: absolute
    background-image: url("../img/tv-627876_1280.jpg")
    background-repeat: no-repeat
    background-position-x: center
    background-position-y: center
    background-size: cover
    background-attachment: fixed
  h1 
    position: absolute
    top: 50%
    left: 0
    right: 0
    transform: translateY(-50%)
    color: white
    z-index: 1
    text-transform: uppercase
    text-align: center
    background-color: rgba(0, 0, 0, 0.4)
    padding: 5vh 0

  &::after 
    position: absolute
    top: 0
    left: 0
    content: ""
    display: block
    height: 100%
    width: 100%
    background-color: rgba(0, 0, 0, 0.4)
    
