.aboutSeries 
  position: fixed
  transform: scale(0)
  transition: 0.3s
  padding-top: 5vh
    
.aboutSeries.active
  transform: scale(1)
  top: 0
  left: 0
  width: 100%
  background-color: white
  display: flex
  flex-direction: column
  align-items: center
  z-index: 1
  transition: 0.3s
  min-height: 100vh
  .back 
    position: absolute
    bottom: 5vw
    right: 5vw
    height: 7vh
    width: 7vh
    z-index: 100
    img 
      height: 100%
      width: 100%
  .tamb 
    width: 320px
    position: relative
    height: 224px
    .tv 
      width: 100%
      position: absolute
      top: 0
      left: 0
      height: 100%
    .tamb-tv 
      width: 69%
      position: absolute
      top: 11%
      left: 9%
      border-radius: 15%
      height: 75%
      overflow: hidden
      .tamb-s 
        width: 100%
  .info 
    position: absolute
    top: 45vh
    text-align: center
    width: 100%
    height: 100% 
    .aboutSeries-item 
      margin-bottom: 5vh
      font-size: 1.2rem
      padding-bottom: 2vh
      padding-top: 2vh
      width: 100%
    &:nth-child(even) 
      background-color: rgb(250, 250, 250)
    .title 
      text-transform: uppercase
      font-family: "CormorantSC"
      padding-bottom: 3vh
      padding-top: 5vh
      font-size: 1.7rem
    .prevEp a,
    .nextEp a 
      font-size: 0.9rem
      color: black
      border-bottom: 1px solid darkblue
